import React from 'react';

import { BsBagFill } from 'react-icons/bs';

const listItem = [
	{ id: '1', title: 'Dashbord', icon: <BsBagFill /> },
	{ id: '2', title: 'UI Elements', icon: <BsBagFill /> },
	{ id: '3', title: 'Components', icon: <BsBagFill /> },
	{ id: '4', title: 'Forms Stuff', icon: <BsBagFill /> },
	{ id: '5', title: 'Data Table', icon: <BsBagFill /> },
	{ id: '6', title: 'Icons', icon: <BsBagFill /> },
	{ id: '7', title: 'Sample Page', icon: <BsBagFill /> },
	{ id: '8', title: 'Extra', icon: <BsBagFill /> },
];

const SideBar = () => {
	return (
		<div className="sidebar-wrapper d-flex w-75 h-100 zindex-10 p-4 justify-items-space-between flex-1">
			<ul style={{ listStyle: 'none', margin: '10px' }}>
				{listItem.map((item) => (
					<>
						<li key={item.id} className="text-center text-primary">
							{item.title}
						</li>
						<li className="text-center text-primary">{item.icon}</li>
					</>
				))}
			</ul>
		</div>
	);
};

export default SideBar;
